import React from "react";
import { Link } from "react-router-dom";
import Style from "./style";

const Card = (props) => {
  const { imgSrc, title, children, style, link, ...restProps } = props || {};
  return link ? (
    <a href={link} target="_blank">
      <Style {...(style ? { style } : {})}>
        <img className="thumbnail" src={imgSrc} loading="lazy" />
        <div className="title">{title}</div>
        {children}
      </Style>
    </a>
  ) : (
    <Style {...(style ? { style } : {})}>
      <img className="thumbnail" src={imgSrc} loading="lazy" />
      <div className="title">{title}</div>
      {children}
    </Style>
  );
};

export default Card;
